import gql from 'graphql-tag'
import { Model as GraphModel } from '@namcbugdb/common'

import { Api } from '../api'
import { Model } from '../../types'

export const fetchModelsQuery = gql`
  query ($offset: Int!) {
    models(limit: 500, offset: $offset) {
      records {
        modelId
        modelName
        abbreviation
        isActive
        predictorCount
      }
      nextOffset
    }
  }
`

export const fetchModels = (api: Api) => async (): Promise<Model[]> => {
  const data = await api.queryAllPages<{ models: { records: GraphModel[] } }>(
    fetchModelsQuery,
    {},
    { dataProperty: 'models' }
  )

  return data.models.records.map(({ modelId: id, modelName: name, ...rest }) => ({ id, name, ...rest }))
}
